<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>You are logged in as: {{ userEmail }}</h2>
    Title: <input type="text" v-model="event.title" placeholder="Title"><br>
    Venue: <select v-model="event.venue">
        <option disabled value="">Please select one</option>
        <option>Salone</option>
        <option>Heritech</option>
    </select>
    <br>
    Curator: <input type="text" v-model="event.curator" placeholder="Curator"><br>
    Location: <input type="text" v-model="event.location" placeholder="Location"><br>
    Description: <textarea v-model="event.description" placeholder="Event description..."></textarea><br>
    <label>Date</label> <input type="date" v-model="event.date" placeholder="Date"><br>
    Start time: <input type="time" v-model="event.startTime">
    End time: <input type="time" v-model="event.endTime"><br>
    <button v-on:click="logout">Logout</button>
    <button v-on:click="saveData">Save Event</button>
    <div class="Message_body" v-if="readDescription">
        <div v-for="line in descriptionLines">{{line}}<br></div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
//require("firebase/firestore");
    
    
export default {
    name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            userEmail: firebase.auth().currentUser.email,
            event: {
                title: "",
                venue: "",
                curator: "",
                date: "",
                startTime: "",
                endTime: "",
                location: "",
                description: "",
            },
            readDescription: ""
        }
    },
    computed: {
        descriptionLines: function() {
            return (this.readDescription || "").split("\n");
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then( ()=> {
                this.$router.replace("login")
            })
        },
        
        saveData: function() {
            /*var db = firebase.firestore();
            const settings = {timestampsInSnapshots: true};
            db.settings(settings);
            
            db.collection("events").add({
                title: this.title,
                location: this.location,
                description: this.description
            })
            .then( (docRef) => {
                console.log("Document written with ID: ", docRef.id);
                docRef.get().then( (doc) => {
                    if (doc.exists) {
                        const data = doc.data();
                        console.log("Document data:", data );
                        this.readDescription = data.description;
                        console.log(this.readDescription);
                        
                    } else {
                        // doc.data() will be undefined in this case
                        console.log("No such document!");
                    }
                }).catch(function(error) {
                    console.log("Error getting document:", error);
                });
            })
            .catch(function(error) {
                console.error("Error adding document: ", error);
            });*/
            
            console.log(this.event);
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



